@tailwind base;

@tailwind components;

@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Days+One&display=swap');

.days-one {
  font-family: 'Days One', sans-serif;
}

.basic-post blockquote {
  border-left: 0.3rem solid #d1d1d1;
  margin-left: 0;
  margin-right: 0;
  padding: 1rem 1.5rem;
}

.basic-post blockquote *:last-child {
  margin-bottom: 0;
}

.basic-post h1,
.basic-post h2,
.basic-post h3,
.basic-post h4,
.basic-post h5,
.basic-post h6 {
  font-weight: 400;
  letter-spacing: -0.1rem;
  margin-bottom: 2rem;
  margin-top: 0;
}

.basic-post h1 {
  font-size: 2.5rem;
  line-height: 1.2;
}

.basic-post h2 {
  font-size: 2.2rem;
  line-height: 1.25;
}

.basic-post h3 {
  font-size: 1.9rem;
  line-height: 1.3;
}

.basic-post h4 {
  font-size: 1.7rem;
  letter-spacing: -0.08rem;
  line-height: 1.35;
}

.basic-post h5 {
  font-size: 1.5rem;
  letter-spacing: -0.05rem;
  line-height: 1.5;
}

.basic-post h6 {
  font-size: 1.2rem;
  letter-spacing: 0;
  line-height: 1.4;
}

.basic-post img {
  max-width: 100%;
}

.basic-post a {
  color: #9b4dca;
  text-decoration: none;
}

.basic-post a:focus,
.basic-post a:hover {
  color: #606c76;
}

.basic-post dl,
.basic-post ol,
.basic-post ul {
  list-style: none;
  margin-top: 0;
  padding-left: 0;
}

.basic-post dl dl,
.basic-post dl ol,
.basic-post dl ul,
.basic-post ol dl,
.basic-post ol ol,
.basic-post ol ul,
.basic-post ul dl,
.basic-post ul ol,
.basic-post ul ul {
  font-size: 90%;
  margin: 1.5rem 0 1.5rem 3rem;
}

.basic-post ol {
  list-style: decimal inside;
}

.basic-post ul {
  list-style: circle inside;
}

.basic-post dd,
.basic-post dt,
.basic-post li {
  margin-bottom: 1rem;
}

.basic-post blockquote,
.basic-post dl,
.basic-post figure,
.basic-post ol,
.basic-post p,
.basic-post pre,
.basic-post table,
.basic-post ul .basic-post hr {
  margin-bottom: 2.5rem;
}
